
* {
  margin: 0;
  padding: 0;
}
html, body, #app {
  height: 100%;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  overflow: hidden;
  -webkit-transition: background .4s;
  transition: background .4s;
  background-color: rgba(0, 0, 0, .8);
}
.actions-wrapper {
  width: 100%;
  -ms-flex-item-align: end;
      align-self: flex-end;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  padding: 10px 30px;
  margin-bottom: 50px;
}
.actions-wrapper button {
  width: 40px;
  height: 40px;
  margin: 0 15px;
  border: none;
  outline: none;
  background:transparent url(data:image/png;base64,) no-repeat;
  cursor: pointer;
  z-index: 100;
}
.actions-wrapper button:disabled {
  background-position-x: 0 !important;
  opacity: .7;
  cursor: default;
}
button.info {
  background-position: 0 -42px;
}
button.prev {
  background-position: 0 -84px;
}
button.next {
  background-position: 0 -126px
}
.actions-wrapper button:enabled:not(.qrcode):hover {
  background-position-x: -252px;
  width: 42px;
  height: 42px;
  margin: -1px 14px;
}
.detail-wrapper {
  background: rgba(0, 0, 0, .8);
  width: 364px;
  height: 100vh;
  padding: 0 30px;
  position: absolute;
  right: -424px;
  top: 0;
  -webkit-transition: right .4s;
  transition: right .4s;
}
.detail-wrapper.show {
  right: 0;
}
.detail-wrapper .detail {
  max-height: calc(100vh - 120px);
  overflow-y: scroll;
  width: 385px;
  overflow-x: hidden;
}
button.qrcode {
  position: relative;
  background: transparent url(data:image/png;base64,) no-repeat;
  background-position: 0 0;
}
.actions-wrapper button.qrcode:hover{
  background-position-x: -40px;
}
button.qrcode .line {
  position: absolute;
  left: calc(50% - 2px);
  bottom: 40px;
  width: 0;
  border: 1px solid #fff;
  height: 70px;
}
button.qrcode .qrcode-img {
  display: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: absolute;
  left: -50px;
  bottom: 110px;
  width: 150px;
  height: 150px;
  background: #fff;
}
button.qrcode:hover .qrcode-img {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
button.qrcode>.line {
  display: none;
}
button.qrcode:hover>.line{
  display: initial;
}
